import { Component, OnInit } from '@angular/core';
import axios from 'axios';
import { api } from '../server';
import * as moment from 'moment';
import { Storage } from '@ionic/storage-angular';

@Component({
  selector: 'app-income',
  templateUrl: './income.page.html',
  styleUrls: ['./income.page.scss'],
})
export class IncomePage implements OnInit {


  tests = [];
  search = {
    from: moment(new Date()).format('YYYY-MM-DD'),
    to: moment(new Date()).format('YYYY-MM-DD'),
    testId: 0
  }
  userinfo:any;
  results = [];
  constructor(
    public storage:Storage
  ) { }

  ngOnInit() {
    axios.get(api + 'tests').then(res => {
      this.tests = res.data
    })

    this.storage.get("user").then(res => {
      this.userinfo = res.data;

    })

  }

  searchTests() {
    let date1 = moment(this.search.from).format('YYYY-MM-DD');
    let date2 = moment(this.search.to).format('YYYY-MM-DD');
    axios.get(api + `income?dateOne=${date1}&dateTwo=${date2}&testId=${this.search.testId}`).then(res => {
      this.results = res.data;
      console.log(this.results);
      
    })
    
  }

  sum() {
    let sum = 0;
    for (let i = 0; i < this.results.length; i++) {
      sum = sum + this.results[i].price
      
    }
    return sum;
  }

  exportToExcel() {
    let excel = []
    for (let i = 0; i < this.results.length; i++) {
      excel.push({
        patientName : this.results[i].name,
        age : moment().diff(this.results[i].dob, 'years',false),
        gender : (this.results[i].gender == 0) ? 'Male' : 'Female',
        address : this.results[i].address,
        testName : this.results[i].testName,
        result : this.results[i].result,
        phone : this.results[i].phone,
        date : moment(this.results[i].requested).format('YYYY-MM-DD HH:mm:ss'),
        price : this.results[i].price,
        testId : this.results[i].idPatientResult,
        userName: this.results[i].userName,
        documentId : this.results[i].documentId
    })
    }
    axios.post(api + 'createExcel', excel).then(res => {
      console.log(res.data);
      window.open(api + res.data.url)
      
    })
    
  }

  fixDate(date) {
    return moment(date).format('YYYY-MM-DD –– hh:mm A')
  }

}
